import React from 'react';
import "./Contact.css"
import { useState, useEffect } from "react"


function Contact() {




    return (

        <div class="container">
            <div class="row">

                <div class="col-6">

                    <div style={{ fontSize: "16px" }} class="col1">

                        <form action="mailto:f.sultanay@gmail.com" method="GET" >
                            <div class="mb-3 col">
                                <label for="nameInput" class="form-label" name="name1">Name</label>
                                <input type="text" class="form-control" id="name1" placeholder="Name" />
                            </div>
                            <div class="mb-3 col">
                                <label for="email" class="form-label" name="email1">Email</label>
                                <input type="email" class="form-control" id="email1" rows="3" placeholder="Email" />
                            </div>
                            <div class="mb-3 col">
                                <label for="message" class="form-label" name="message1">Message</label>
                                <textarea class="form-control" id="message1" rows="3" placeholder="Please type your message"></textarea>
                            </div>
                            <div class="text-center">

                                {/* <input type="submit" name="submit" value="Send" /> */}

                                <button type="submit" name="submit" value="Send" class="btn btn-success">submit</button>
                                {/* <button><a href="mailto:f.sultanay@gmail.com"></a> Button</button> */}


                            </div>
                        </form>
                    </div>
                </div>

            </div>
        </div>


    );
}

export default Contact;