import React from 'react';
import "./Portfolio.css"
import project from "./projects.json"
import { Rate, Carousel } from 'antd';

import 'antd/dist/antd.css';


function Portfolio() {




    return (


        <div class="container-fluid">
            <h1 id="portfolio"  >Portfolio</h1>
            <div class="row justify-content-md-center justify-content-xs-center justify-content-lg-start ">


                <Carousel autoplay>
                    {project.map(data => (

                        <div class="col-lg-4 col-md-6 col-xs-12  text-center mb-5">


                            <a href={data.githubLink}>
                                <h3 id="h1"> {data.name} </h3>
                            </a>

                            <div style={{ marginLeft: "auto", marginRight: "auto" }} >
                                
                             <a target="_blank" href={data.deployed} > <img className="image" src={data.picture}
                                    style={{ marginLeft: "auto", marginRight: "auto" }} /> 

                                    </a>

                         

                            </div>

                            <Rate allowHalf defaultValue={5} />


                        </div>

                    ))}
                </Carousel>
            </div>
        </div>
    );
}

export default Portfolio;