import React from "react";
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";
import "./Navbar.css"


function Navbar() {
    return (
    
        <nav class="navbar navbar-expand-lg navbar-light">

        <div class='container-fluid'>

            <a id="name" class="navbar-brand" 
                to="/">&nbsp; Fatih Sultan Ay &nbsp;</a>

            <button class="navbar-toggler collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="navbar-collapse justify-content-end collapse" id="navbarSupportedContent">
                <ul class="navbar-nav ">
                    <li class="nav-item">
                    <NavLink to="/" className="nav-link" activeClassName="active">About</NavLink>
                    </li>
                    <li class="nav-item">
                    <NavLink to="/portfolio" className="nav-link" activeClassName="active">Portfolio</NavLink>
                    </li>
                    <li class="nav-item">
                    <NavLink to="/contact" className="nav-link" activeClassName="active">Contact</NavLink>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
    );
}
export default Navbar;




